* {
	user-select: none;
	outline: none;
	-webkit-tap-highlight-color: transparent;
	text-decoration: none;
	touch-action: none;
}

body {
	background-color: #000;
	margin: 0px;
	overflow: hidden;
}

.click_text {
	color: transparent;
	line-height: 40px;
	position: fixed;
	z-index: 27;
	width: 200px;
	text-align: center;
	white-space: nowrap;
	top: calc(50vh + 60px);
	left: calc(50vw - 100px);
	transition-property: color;
	transition-duration: 0.3s;
	transition-delay: 0.1s;
	opacity: 0.75;
}

model-viewer {
	width: 100vw;
	height: 100vh;
}